#wrapper {
  display: flex;
  flex-direction: row;
  align-content: center;
  justify-content: center;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
}

#board {
  padding: 32px;
  border: 5px solid black;
  aspect-ratio: 1 / 1;
  width: min(80vh, 60%);
}
table {
  aspect-ratio: 1 / 1;
  width: 100%;
  padding: 0;
  border: none;
  height: auto;
}
tr {
  padding: none;
  margin: 0;
}
img {
  display: block;
  margin: none;
  padding: none;
  width: 100%;
  height: 100%;
}

p {
  color: red;
}

td {
  aspect-ratio: 1 / 1;
}
td.grid {
  border: 1px dashed gray;
  border-collapse: separate;
  padding: 0;
  margin: 0;
  padding-bottom: 0px;
}

td.out {
  border-collapse: separate;
  padding: 0;
  padding-bottom: 0px;
}



table, tbody {
  border-collapse: separate;
  padding: 0;
  margin: 0;
  padding-bottom: 0px;
}

#bottom {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  padding: 32px;
}
